<template>
    <div class="dock-video">
        <NavBar
            ref="navbar"
            :title="pagedata.title" />
        <MiVideo
            :c-s-style="CSStyle"
            :duration="pagedata.duration"
            :poster="pagedata.poster"
            @ended="videoEnd"
            ref="videoPlayer"
            :video-url="pagedata.videoUrl" />
        <GrayMask :show="showLockScreen">
            <div class="video-end">
                <Button
                    @click="goSystemSet"
                    class="video-end-button">立即开启</Button>
                <div
                    @click="replay"
                    class="video-replay">重新播放</div>
            </div>
        </GrayMask>
    </div>
</template>

<script>
import NavBar from '@/components/NavBar';
import Button from '@/components/Button';
import GrayMask from '@/components/GrayMask';
import MiVideo from '@/components/MiVideo';
import { goIndexTab } from '@/utils/jsApiV2/deeplink';

export default {
    data() {
        return {
            CSStyle: {
                height: '0px',
            },
            showLockScreen: false,
        };
    },
    computed: {
        pagedata() {
            const { type, phone } = this.$route.query;
            // 锁屏任务教程
            if (type === 'lockscreen') {
                const videoData = {
                    title: '锁屏权限开启',
                    poster: 'http://t1.market.xiaomi.com/download/Browser/0a7034818c572b5f56c5b85f5d07e418b9c415f5b',
                    videoUrl: 'http://t1.market.xiaomi.com/download/Browser/0449dd4bc7b6b439b15deb5f0799cfd62c1c486a7',
                    duration: 9,
                };
                if (phone === 'vivo') {
                    videoData.videoUrl = 'http://t1.market.xiaomi.com/download/Browser/0749d4cbc6b1bd39c15de75f0189c1d623741a7fd';
                    videoData.duration = 11;
                }
                return videoData;
            }
            // dock栏任务视频教程
            return {
                title: '添加想看App至快捷入口',
                poster: 'http://t1.market.xiaomi.com/download/Browser/0e897456bb6684d0f0a16b3b2a6efda31954311cd',
                videoUrl: 'http://t1.market.xiaomi.com/download/Browser/0b20e4569fe8bc35b7a93006507368ca8c64160b4',
                duration: 26,
            };
        },
    },
    components: {
        NavBar,
        MiVideo,
        GrayMask,
        Button,
    },
    methods: {
        videoEnd() {
            const { type } = this.$route.query;
            // 锁屏任务教程
            if (type === 'lockscreen') {
                this.showLockScreen = true;
            }
        },
        // 点击立即开启
        goSystemSet() {
            goIndexTab('permissionSetting');
            this.showLockScreen = false;
            this.$o2o({
                e_c: '锁屏引导教程',
                e_a: '点击',
                e_n: '立即开启',
            });
        },
        replay() {
            this.$refs.videoPlayer.player.play();
            this.showLockScreen = false;
            this.$o2o({
                e_c: '锁屏引导教程',
                e_a: '点击',
                e_n: '重新播放',
            });
        },
    },
    mounted() {
        this.CSStyle.height = `${document.documentElement.clientHeight - this.$refs.navbar.$el.clientHeight}px`;
        this.CSStyle.width = `${document.documentElement.clientWidth}px`;
        this.$root.hidePlaceholder();
        // 锁屏任务教程
        if (this.$route.query.type === 'lockscreen') {
            this.$o2o({
                e_c: '锁屏引导教程',
                e_a: '曝光',
                e_n: '教程曝光',
            });
        }
    },
};
</script>
<style scoped>
.dock-video >>> .placeholder-poster img {
    object-fit: cover;
}

.video-end {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
}

.dock-video .video-end-button {
    width: 168px;
    height: 49px;
    background: rgba(255, 74, 47, 1);
    border-radius: 3px;
    line-height: 20px;
}

.video-replay::before {
    display: inline-block;
    content: "";
    height: 20px;
    width: 20px;
    background: url(./image/replay.webp) no-repeat;
    background-size: contain;
    margin-right: 10px;
    vertical-align: middle;
    transform: translateY(-2px);
}

.video-replay {
    text-align: center;
    width: 117px;
    font: 15px/20px miui;
    color: rgba(255, 255, 255, 1);
    margin: 32px auto 0;
}
</style>
